.App {
  text-align: center;
  position: relative;
}

.loginPage{
  display: flex;
  height: 100vh;
}

.loginPanel{
  flex: 1;
}
.loginWrapper{
  display: flex;
}

.menuIcon{
  position: absolute;
  top: 5px;
  font-size: 18px;
  right: 5px;
  display: none;
}

.aboutPage{
  flex: 1;
  background-image: url(https://images.unsplash.com/photo-1582020086775-1c3fc831a1c7?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max);
  height: 100vh;
  background-size: cover;
}

.aboutTitle{
  color: white;
}

.storeInfoPage{
  background-image: url('https://ak1.ostkcdn.com/images/products/is/images/direct/12f3ac9d26a9cd14bf4c40a2a953b985b88c58df/Costway-Ab-Fitness-Crunch-Abdominal-Exercise-Workout-Machine-Glider-Roller-Pushup.jpg?impolicy=medium');
  background-repeat: no-repeat;
  background-position: center;
  flex: 1;
  height: 100vh;
}

.infoDetails{
  font-weight: bold;
  border-radius: 6px;
  color: white;
  padding: 10px;
  background-color: black;
  display: inline-block;
  line-height: 25px;
}

.registerPage{
  flex: 1;
  overflow: auto;
}

.paddingClass{
  padding: 10px;
}

.errMsg{
  display:inline-block;
  padding:5px 6px 8px 6px;
  border-radius: 6px;
  color:white;
  background-color:red;
  position: relative;
}

.chat:after {
  content: "";
  position:absolute;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 12px solid red;
  transform:rotate(-45deg);
  right: -13px; 
  top: 2px;
}

.storeParagraph{
  display:inline-block;
  padding:5px 6px 8px 6px;
  margin: 5px;
  border-radius: 6px;
  color:black;
  background-color: bisque;
  position: relative;
  font-weight: bold;
}

.storeInfoTitle{
    padding: 10px;
    background-image: url('https://ak5.picdn.net/shutterstock/videos/6584225/thumb/1.jpg');
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 40px;
    font-family: Titillium Web,sans-serif;
    font-style: italic;
    margin: 0;
    line-height: 1;
}

.numOfProducts{
  font-family: Titillium Web,sans-serif;
  font-weight: bold;
}

.logo{
  position: absolute;
  right: 10px;
  top: 10px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.fontsize{
  font-size: 50px;
}

.Shop{
  background-color: bisque;
  flex: 3;
  position: relative;
}

.ShopPage{
  display: flex;
  min-height: 100vh;
}

.searchProduct{
margin: 0 auto !important;
}

.categoryTitles{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px;
}

.divider{
    background-color: gray;
    height: 1px;
    transform: rotate(90deg);
    display: inherit;
    width: 22px;
    position: absolute;
    right: -18px;
    top: 14px;
  }

.specificCategory{
  margin-left: 15px;  
  cursor: pointer;
  font-weight: bold;
}

.categoryWrapper{
  position: relative;
}

.size11{
  font-size: 11px;
}

.productsList{
  margin-top: 50px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.addButton{
  margin: auto !important;
}

.qunatityModal{
  margin: 0px 4px;
  user-select: none;
}

.qunatityDiv{
  text-align: center;
}

.qunatityBtn{
  font-size: 11px;
  background-color: lightgray;
  cursor: pointer;
  border-radius: 50%;
  padding: 2px;
}
.closeModal{
  position: absolute;
  top: 2px;
  right: 3px;
  cursor: pointer;
}

.imgModal{
  width: 200px;
  height: 200px;
}

.productModal{
  font-weight: bold;
  text-align: center;
  margin: 10px;
}

.addBtnModal{
  text-align: center;
  margin-top: 5px;
}

.categoryAll{
  position: relative;
}

.myCartPanel{
  background-color: #8d877c;
  overflow-y: auto;
}

.flex1{
  flex: 1;
  transition: 0.3s;
}

.flex0{
  flex: 0;
  transition: 0.3s;
}

.cartImg{
  width: 50px;
  height: 50px;
}

.padding{
  padding: 2px !important;
}

.fontSize{
  font-size: 13px !important;
}

.totalPrice{
  text-align: center;
  font-weight: bold;
  font-style: italic;
  margin-top: 15px;
  padding: 15px;
}

.openCartBtn{
  cursor: pointer !important;
  position: absolute;
  width: 80px;
  height: 80px;
  top: 120px;
  left: -40px;
  background-color: #8d877c;
  border-radius: 50%;
}

.shoppingIcon{
  font-size: 18px ;
  margin-top: 30px;
  margin-left: 30px;
}

.cartAtOrder{
  flex: 0.4;
}

.orderPage{
  flex:1;
  background-color: bisque;
  /* height: 100vh; */
}

.autosuggest{
  width: 50%;
  min-height: 10%;
  border: 1px solid grey;
  background-color: white;
  margin: 0 auto;
}

.red{background-color: yellow;}

.backToShop{
  position: absolute !important;
  right: 10px !important;
  top: 10px !important;
}

.sectionTitle{
  width: 150px;
  display: inline-block;
}

.confirmBtn{
  margin-top: 25px !important;
}

.lineModal{
    width: 3px;
    height: 100%;
    background-color: #3f51b5;
    position: absolute;
    left: 0px;
    top: 0px;
}

.tooltip {
  position: relative;  
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  top: -50px;
  right: 325px;
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.black:after{
  border-bottom: 12px solid black;
  transform: rotate(50deg);
  top: 70px;
  right: 40px;
}

.successMsg{
  text-align: center;
  font-weight: bold;
}

.width111{
  width: 111px;
}

.voucherBtn{
  margin: 0 auto;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.leftMargin{
  margin-left: 10px;
}

.logoutWrapper{
  position: absolute;
  top : 15px;
  left: 15px;
  font-weight: bold;
  color: #3f51b5
}

.logoutBtn{
  cursor: pointer;
}

.logoutBtn:visited {
  color: #3f51b5
}

.saveBtn{
  margin-top: 30px !important;
}

.imageAdd{
  width: 75px;
  height: 75px;
  border-radius: 50%;
  margin-top: 50px;
  cursor: pointer;
}

.loaderWrapper{
    width: 100%;
    height: 100%;
    background-color: black;
    z-index: 9999;
    position: absolute;
}

.payBtn{
    width: 170px;
    border-radius: 10px;
}

.payWrapperBtn{
    position: fixed;
    right: 10%;
    top: 13%;
    z-index: 90;
}

.circle1{
  border-radius: 50%;
  background-color: black;
  height: 50px;
  width: 50px;
  margin-left: 130px;
}

.circle2{
  border-radius: 50%;
  background-color: black;
  height: 40px;
  width: 40px;
  margin-left: 120px;
}

.circle3{
  border-radius: 50%;
  background-color: black;
  height: 30px;
  width: 30px;
  margin-left: 110px;
}

.underline{
  text-decoration: underline;
  padding-right: 5px;
}

@media screen and (max-width: 1200px) {
  .menuBar{
    position: absolute;
    right: 0px;
    top:0px;
    width: 0%;
    border-radius: 10px;
    display: flex;
      flex-direction: column;
      background-color: white;
    }
    
    .loginPage{
      display: inline-block;
    }
    .menuIcon{
      display: inline-block;
      z-index: 999;
    }
    .width80{
      transition: 0.3s;
      width: 80%;
      border: black 1px solid;
    }
    .inlineBlock{
      display: inline-block !important;
    }
    
    .menuBtn{
      width: 160px;
      margin: 0 auto;
      font-weight: bold;
      display: none;
    }

    .lineMenu{
      height: 1px;
      margin: 0 auto;
      width: 65%;
      background-color: black;
    }
    .aboutTitle{
      margin: 0;
    }
    .storeInfoPage{
      width: 100vw;
    }
    .fontsize{
      font-size: 20px !important;
    }
    .logo{
      width: 45px;
      height: 45px;
    }
    .specificCard{
      width: 150px !important;
    }
    .logoutWrapper{
      font-size: 12px;
    }
    .payWrapperBtn{
      top: 28%;
    }
    .payBtn{
      width: 100px;
    }
    .openCartBtn{
      margin-top: 75px;
      height: 70px;
      width: 70px;
    }
    .fa-shopping-cart {
      font-size: 15px;
      margin-left: 39px;
    }
    .fa-arrow-right{
      font-size: 15px;
    }
    .glassField{
      width: 300px !important;
    }
    .displayNone{
      display: none ;
    }
    .payWrapperBtn{
     position: static;
     margin-top: 10px;
     display: inline-block;
    }
    .backToShop{
      margin-top: 20px !important;
      position: static !important;
    }
    .confirmBtn{
      margin-bottom: 20px !important;
    }
    .marginBottom{
      margin-bottom: 15px !important;
    }
    .tooltiptext{
      font-size: 12px;
      right: 18px !important;
      top: -8px !important;
    }
    .black:after{
      top: 35px;
    }
    .loadGif{
      width: 100px;
    }
}